
import React from 'react'
import './StateBuilder.css'
import Canvas from '../Canvas'
import StateEditor from '../StateEditor'
import _ from 'lodash'
import util from '../../services/util'

class StateBuilder extends React.Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  update(component) {
    this.props.update(component)
  }

  addState() {
    let states = _.cloneDeep(this.props.component.states)

    let id = util.hashString()

    states.push({
      id: id,
      title: `State ${states.length + 1}`,
      context_id_list: []
    })

    states = _.map(states, (state) => {
      if (state.id === id) {
        state.expanded = true
      }

      state.selected = state.id === id
      return state
    })

    this.update({
      states: states
    })
  }

  render() {
    let component = this.props.component

    return (
      <div className="StateBuilder">
        <div className="state-selector">
          <ul className="state-editor-menu">
            {component.states.map((state) => {
              return (
                <li key={state.id} className="state-editor-item">
                  <StateEditor component={component} stateId={state.id} update={(component) => this.update(component)}></StateEditor>
                </li>
              )
            })}
          </ul>

          <button className="add-state" onClick={() => this.addState()}>
            add state
          </button>
        </div>

        <Canvas stateView={true} component={component}></Canvas>
      </div>
    )
  }
}

export default StateBuilder
