
import React from 'react'
import './Tutorial.css'

class Tutorial extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      sound: true,
      playIndex: 0
    }

    if (props.tutorial) {
      window.setTimeout(() => {
        this.play()
      }, 1000)
    }
  }

  back() {
    this.setState((prevState) => {
      return {
        playIndex: prevState.playIndex - 1
      }
    }, () => {
      this.play()
    })
  }

  forward() {
    this.setState((prevState) => {
      return {
        playIndex: prevState.playIndex + 1
      }
    }, () => {
      this.play()
    })
  }

  play() {
    if (this.state.sound) {
      window.responsiveVoice.speak(this.props.tutorial.instructions[this.state.playIndex])
    }
  }

  pause() {
    window.responsiveVoice.pause()
  }

  toggleSound() {
    if (this.state.sound) {
      this.pause()
    }
    this.setState({sound: !this.state.sound})
  }

  render() {
    let tutorial = this.props.tutorial

    if (!tutorial) {
      return null
    }

    return (
      <div className="Tutorial">
        <button disabled={this.state.playIndex === 0} className="tutorial-btn" onClick={() => this.back()}>
          <i className="fa fa-arrow-left"></i>
        </button>

        <p className="tutorial-message">
          {tutorial.instructions[this.state.playIndex]}
        </p>

        <div className="tutorial-cta">
          <button className="tutorial-btn" onClick={() => this.toggleSound()}>
            <i className={`fa fa-microphone${this.state.sound ? '' : '-slash'}`}></i>
          </button>

          <button className="tutorial-btn" onClick={() => this.play()}>
            <i className="fa fa-play"></i>
          </button>

          <button className="tutorial-btn" onClick={() => this.pause()}>
            <i className="fa fa-pause"></i>
          </button>

          <button disabled={this.state.playIndex === tutorial.instructions.length - 1} className="tutorial-btn" onClick={() => this.forward()}>
            <i className="fa fa-arrow-right"></i>
          </button>
        </div>
      </div>
    )
  }
}

export default Tutorial
