
import React from 'react'
import './Test.css'
import Setup from '../Setup'
import NavTab from '../NavTab'
import Tutorial from '../Tutorial'
import tutorials from '../../services/tutorials'

class Test extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      tutorial: null,
      component: null
    }
  }

  loadTutorial(tutorial) {
    this.setState({tutorial: tutorial})
  }

  render() {
    return (
      <div className="Test">
        {(() => {
          if (this.state.tutorial) {
            return (
              <Tutorial tutorial={this.state.tutorial}></Tutorial>
            )
          }

          return null
        })()}

        {(() => {
          if (this.state.component) {
            return (
              <NavTab component={this.state.component}></NavTab>
            )
          }

          return (
            <Setup tutorial={(tutorial) => this.loadTutorial(tutorial)} begin={(component) => this.setState({component: component})}></Setup>
          )
        })()}
      </div>
    )
  }
}

export default Test
