
import React from 'react'
import './StateEditor.css'
import _ from 'lodash'

class StateEditor extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      expanded: false
    }
  }

  select() {
    let states = _.map(this.props.component.states, (state) => {
      state.selected = state.id === this.props.stateId ? !state.selected : false
      return state
    })

    this.props.update({states: states})
  }

  expand(bool) {
    this.setState((prevState) => {
      return {
        expanded: bool === undefined ? !prevState.expanded : bool
      }
    })
  }

  remove() {
    let states = _.filter(this.props.component.states, (state) => {
      return state.id !== this.props.stateId
    })

    this.props.update({states: states})
  }

  updateTitle(title) {
    let states = _.map(this.props.component.states, (state) => {
      state.title = state.id === this.props.stateId ? title : state.title
      return state
    })

    this.props.update({states: states})
  }

  appendContext(contextId) {
    let states = _.map(this.props.component.states, (state) => {
      if (state.id !== this.props.stateId) {
        return state
      }

      state.context_id_list.push(contextId)

      return state
    })

    this.props.update({states: states})
  }

  removeContext(ctxId) {
    let states = _.map(this.props.component.states, (state) => {
      if (state.id !== this.props.stateId) {
        return state
      }

      state.context_id_list = _.filter(state.context_id_list, (id) => {
        return id !== ctxId
      })

      return state
    })

    this.props.update({states: states})
  }

  render() {
    let component = this.props.component
    let state = _.find(component.states, {id: this.props.stateId})
    let contexts = _.compact(_.map(state.context_id_list, (ctxId) => {
      let ctx = _.find(component.contexts, {id: ctxId})
      return ctx
    }))

    return (
      <div className={`StateEditor
        ${this.state.expanded ? 'expanded' : ''}
        ${state.selected ? 'selected' : ''}
      `}>
        <h3 className="state-header">
          <i className="state-header-icon indicator" onClick={() => this.select()}>
            <i className="fa fa-eye"></i>
          </i>

          <input className="state-header-input" type="text" value={state.title} onChange={(e) => this.updateTitle(e.target.value)} />

          <i onClick={() => this.expand()} className={`fa fa-chevron-${this.state.expanded ? 'up' : 'down'} state-header-icon expand`}></i>
          <i onClick={() => this.remove()} className="fa fa-minus state-header-icon remove"></i>
        </h3>

        <div className="state-body">
          <ul className="state-context-menu">
            {contexts.map((ctx) => {
              return (
                <li key={ctx.id} className="state-context-item">
                  <span className="state-context-title">{ctx.title}</span>
                  <i className="fa fa-minus state-context-icon" onClick={() => this.removeContext(ctx.id)}></i>
                </li>
              )
            })}
          </ul>

          <select className="state-context-select" value={''} onChange={(e) => this.appendContext(e.target.value)}>
            <option value="">add a context</option>
            {component.contexts.map((ctx) => {
              return (
                <option disabled={_.findIndex(contexts, {id: ctx.id}) >= 0} key={ctx.id} className="state-context-item" value={ctx.id}>{ctx.title}</option>
              )
            })}
          </select>
        </div>
      </div>
    )
  }
}

export default StateEditor
