
import React from 'react'
import './StyleEditor.css'
import _ from 'lodash'

class StyleEditor extends React.Component {

  constructor(props) {
    super(props)

    this.availableDomKeys = [
      'width',
      'height',
      'backgroundColor',
      'color',
      'textContent',
      'paddingBottom',
      'paddingTop',
      'paddingRight',
      'paddingLeft',
      'borderTop',
      'borderRight',
      'borderBottom',
      'borderLeft',
      'marginTop',
      'marginLeft',
      'marginBottom',
      'marginRight',
      'borderTopRightRadius',
      'borderTopLeftRadius',
      'borderBottomRightRadius',
      'borderBottomLeftRadius',
      'display',
      'opacity',
      'visibility',
      'flexDirection',
      'flexWrap',
      'flex',
      'boxShadow',
      'textShadow',
      'backgroundImage',
      'background',
      'fontSize',
      'fontFamily',
      'lineHeight',
      'fontStyle',
      'fontWeight',
      'textDecoration',
      'outline',
      'textAlign',
      'overflowX',
      'overflowY',
      'maxHeight',
      'maxWidth',
      'padding',
      'border',
      'margin'
    ]

    this.state = {
      focused: false
    }
  }

  updateKey(key) {
    let style = _.cloneDeep(this.props.style)
    style.domKey = key

    this.props.update(style)

    this.setState({focused: false})
  }

  updateValue(value) {
    let style = _.cloneDeep(this.props.style)
    style.domValue = value

    this.props.update(style)
  }

  focus() {
    this.setState({
      focused: true
    })
  }

  blur() {
    this.setState({
      focused: false
    })
  }

  propKeyDown(e) {
    let style = _.cloneDeep(this.props.style)

    if (e.keyCode === 8 && !style.domKey) {
      this.props.remove(style.id)
    }
  }

  render() {
    let style = this.props.style
    let domKeys = _.sortBy(this.availableDomKeys)

    return (
      <div className="StyleEditor">
        <div className="style-input dom-key">
          <input
            className={this.state.focused ? 'focused' : ''}
            onKeyDown={(e) => this.propKeyDown(e)}
            onBlur={() => this.blur()}
            onFocus={() => this.focus()}
            placeholder="enter a style property"
            type="text"
            value={style.domKey}
            onChange={(e) => this.updateKey(e.target.value)}
          />

          <ul className="key-dropdown">
            {domKeys.map((domKey) => {
              return (
                <li key={domKey} onMouseDown={() =>  this.updateKey(domKey)} className="key-item">{domKey}</li>
              )
            })}
          </ul>
        </div>
        <div className="style-input dom-value">
          <input placeholder="enter a style value" type="text" value={style.domValue} onChange={(e) => this.updateValue(e.target.value)} />
        </div>
      </div>
    )
  }

}

export default StyleEditor
