
import React from 'react'
import './Canvas.css'
import Mock from '../Mock'

class Canvas extends React.Component {

  constructor(props) {
    super(props)

    this.state = {}
  }

  render() {
    return (
      <div className="Canvas">
        {(() => {
          if (!this.props.component || !this.props.component.elements.length) {
            return null
          }

          return (
            <Mock component={this.props.component} contextView={this.props.contextView} stateView={this.props.stateView}></Mock>
          )
        })()}
      </div>
    )
  }

}

export default Canvas
