
import React from 'react'
import './Mock.css'
import _ from 'lodash'

class Mock extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      stateView: !!props.stateView,
      contextView: !!props.contextView
    }
  }

  appendBaseStyles(styles, elementId) {
    let swatches = _.filter(this.props.component.swatches, {element_id: elementId, context_id: null})

    swatches.map((swatch) => {
      let elementStyles = _.filter(this.props.component.styles, {swatch_id: swatch.id})

      elementStyles.map((style) => {
        if (style.domValue !== '') {
          styles[style.domKey] = style.domValue
        }

        return null
      })
      return null
    })
  }

  appendStateStyles(styles, elementId, state) {
    let contexts = _.map(state.context_id_list, (ctxId) => {
      return _.find(this.props.component.contexts, {id: ctxId})
    })

    contexts.map((ctx) => {
      this.appendContextStyles(styles, elementId, ctx)
      return null
    })
  }

  appendContextStyles(styles, elementId, context) {
    let swatches = _.filter(this.props.component.swatches, {element_id: elementId, context_id: context.id})

    swatches.map((swatch) => {
      let elementStyles = _.filter(this.props.component.styles, {swatch_id: swatch.id})

      elementStyles.map((style) => {
        if (style.domValue !== '') {
          styles[style.domKey] = style.domValue
        }

        return null
      })
      return null
    })
  }

  renderElement(elements, element, state, context) {
    element = element || elements[0]
    let children = _.filter(elements, {parent: element.id})

    let childrenElems

    if (children.length) {
      childrenElems = _.map(children, (childElem) => {
        return this.renderElement(elements, childElem, state, context)
      })
    }

    let styles = {}
    this.appendBaseStyles(styles, element.id)

    if (context) {
      this.appendContextStyles(styles, element.id, context)
    }

    if (state) {
      this.appendStateStyles(styles, element.id, state)
    }

    return (
      <div style={styles} title={element.title} key={element.id} className="Element">
        {styles.textContent || null}
        {childrenElems}
      </div>
    )
  }

  render() {
    let elements = this.props.component.elements

    let context = null
    if (this.props.contextView) {
      context = _.find(this.props.component.contexts, {selected: true})
    }

    let state = null
    if (this.props.stateView) {
      state = _.find(this.props.component.states, {selected: true})
    }

    return (
      <div className="Mock">
        {this.renderElement(elements, null, state, context)}
      </div>
    )
  }

}

export default Mock
