
import React from 'react'
import './SwatchBuilder.css'
import _ from 'lodash'
import util from '../../services/util'
import SwatchEditor from '../SwatchEditor'

class SwatchBuilder extends React.Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  update(swatches) {
    this.props.update({swatches: swatches})
  }

  addSwatch() {
    let element = _.find(this.props.component.elements, {selected: true})
    let context = _.find(this.props.component.contexts, {selected: true})
    let contextId = context ? context.id : null

    if (!element) {
      return
    }

    let swatches = _.filter(this.props.component.swatches, (swatch) => {
      return swatch.element_id === element.id && swatch.context_id === contextId
    })

    let title = element.title
    if (swatches.length) {
      title = 'Untitled Swatch'
    }

    if (contextId) {
      title += ` - ${context.title}`
    }

    this.update(this.props.component.swatches.concat([
      {
        id: util.hashString(),
        title: title,
        element_id: element.id,
        context_id: context ? context.id : null
      }
    ]))
  }

  removeSwatch(id) {
    let styles = _.filter(this.props.component.styles, (style) => {
      return style.swatch_id !== id
    })

    let swatches = _.filter(this.props.component.swatches, (swatch) => {
      return swatch.id !== id
    })

    this.props.update({
      swatches: swatches,
      styles: styles
    })
  }

  renderContainer(element, swatches) {
    swatches = swatches || []

    return (
      <div className="SwatchBuilder">
        {swatches.map((swatch) => {
          return (
            <SwatchEditor remove={(id) => this.removeSwatch(id)} update={(component) => this.props.update(component)} key={swatch.id} component={this.props.component} swatch={swatch}></SwatchEditor>
          )
        })}

        <button className={`add-swatch ${element ? '' : 'disabled'}`} onClick={() => this.addSwatch()}>
          add swatch
        </button>
      </div>
    )
  }

  render() {
    let context = _.find(this.props.component.contexts, {selected: true}) || {}
    let element = _.find(this.props.component.elements, {selected: true})

    if (!element) {
      return this.renderContainer()
    }

    let swatches = _.filter(this.props.component.swatches, {element_id: element.id, context_id: context.id || null})

    return this.renderContainer(element, swatches)
  }
}

export default SwatchBuilder
