
import React from 'react'
import './NavTab.css'
import _ from 'lodash'
import ElementBuilder from '../ElementBuilder'
import ElementSelector from '../ElementSelector'
import Canvas from '../Canvas'
import SwatchBuilder from '../SwatchBuilder'
import Save from '../Save'
import ContextBuilder from '../ContextBuilder'
import ContextSelector from '../ContextSelector'
import StateBuilder from '../StateBuilder'
import Preview from '../Preview'

class NavTab extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      component: props.component,
      tab: 'elements',
      tabs: [
        {
          id: 'elements',
          title: 'Elements',
          tag: 'Build'
        },
        {
          id: 'context',
          title: 'Context',
          tag: 'Build'
        },
        {
          id: 'style',
          title: 'Style',
          tag: 'Design'
        },
        {
          id: 'state',
          title: 'State',
          tag: 'Design'
        },
        {
          id: 'inspect',
          title: 'Inspect',
          tag: 'View'
        },
        {
          id: 'preview',
          title: 'Preview',
          tag: 'View'
        }
      ]
    }
  }

  updateTitle(title) {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)

      component.title = title

      return {
        component: component
      }
    })
  }

  render_elements() {
    return (
      <div className="build">
        <ElementBuilder component={this.state.component} update={(component) => this.updateComponent(component)}></ElementBuilder>
      </div>
    )
  }

  render_context() {
    return (
      <div className="context">
        <ContextBuilder component={this.state.component} update={(component) => this.updateComponent(component)}></ContextBuilder>
      </div>
    )
  }

  render_style() {
    return (
      <div className="style">
        <div className="style-section">
          <h4 className="subtitle">Element</h4>
          <ElementSelector update={(component) => this.updateComponent(component)} component={this.state.component}></ElementSelector>
        </div>
        <div className="style-section">
          <h4 className="subtitle">Context</h4>
          <ContextSelector update={(component) => this.updateComponent(component)} component={this.state.component}></ContextSelector>
        </div>
        <div className="style-section expand">
          <h4 className="subtitle">Canvas</h4>
          <Canvas contextView={true} component={this.state.component}></Canvas>
        </div>
        <div className="style-section">
          <h4 className="subtitle">Swatch</h4>
          <SwatchBuilder update={(component) => this.updateComponent(component)} filterContext={false} component={this.state.component}></SwatchBuilder>
        </div>
      </div>
    )
  }

  render_state() {
    return (
      <div className="state">
        <StateBuilder component={this.state.component} update={(component) => this.updateComponent(component)}></StateBuilder>
      </div>
    )
  }

  render_inspect() {
    return (
      <div className="inspect">
        <p>Coming soon</p>
      </div>
    )
  }

  render_preview() {
    return (
      <div className="preview">
        <Preview component={this.state.component}></Preview>
      </div>
    )
  }

  updateComponent(component) {
    this.setState((prevState) => {
      let prevComponent = prevState.component

      _.merge(prevComponent, component)

      _.forIn(component, (v, k) => {
        prevComponent[k] = v
      })

      return {
        component: prevComponent
      }
    })
  }

  selectTab(tabId) {
    this.setState({tab: tabId})
  }

  render() {
    return (
      <div className="NavTab">
        <div className="tab-header">
          <div className="meta-info">
            <input placeholder="Enter a name" type="text" value={this.state.component.title} onChange={(e) => this.updateTitle(e.target.value)} />
            <Save component={this.state.component}></Save>
          </div>


          <ul className="tab-header-list">
            {this.state.tabs.map((tab) => {
              return (
                <li
                  key={`t_${tab.id}`}
                  onClick={() => this.selectTab(tab.id)}
                  className={`tab-header-item ${tab.id === this.state.tab ? 'active' : ''}`}>
                  <small className={`tab-header-sub-title ${tab.tag}`}>{tab.tag}</small>
                  {tab.id === 'setup' && this.state.component.title ? this.state.component.title : tab.title}
                </li>
              )
            })}
          </ul>
        </div>

        <div className="tab-body">
          {this[`render_${this.state.tab}`]()}
        </div>
      </div>
    )
  }

}

export default NavTab
