
import React from 'react'
import './Save.css'
import _ from 'lodash'

class Save extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      needsUpdate: false,
      commanding: false
    }

    window.onkeydown = (e) => {
      if (e.keyCode === 83 && this.state.commanding) {
        e.preventDefault()

        this.save()
        this.setState({commanding: false})
      }

      if (e.keyCode === 91) {
        this.setState({commanding: true})
      }
    }

    window.onkeyup = (e) => {
      if (e.keyCode === 91) {
        this.setState({commanding: false})
      }
    }
  }

  componentWillReceiveProps(props) {
    this.setState({needsUpdate: true})
  }

  save() {
    let components = window.localStorage.getItem('components')
    components = components ? JSON.parse(components) : []

    let index = _.findIndex(components, {id: this.props.component.id})

    if (index >= 0) {
      components[index] = this.props.component
    } else {
      components.push(this.props.component)
    }

    window.localStorage.setItem('components', JSON.stringify(components))

    this.setState({needsUpdate: false})
  }

  render() {
    return (
      <button className={`Save ${this.state.needsUpdate ? '' : 'disabled'}`} onClick={() => this.save()}>
        Save
      </button>
    )
  }

}

export default Save
