
import React from 'react'
import './Setup.css'
import _ from 'lodash'
import util from '../../services/util'
import tutorials from '../../services/tutorials'

class Setup extends React.Component {

  constructor(props) {
    super(props)

    let components = window.localStorage.getItem('components')
    components = components ? JSON.parse(components) : []

    this.state = {
      tutorials: tutorials,
      showTutorials: false,
      showLoad: false,
      components: components,
      component: {
        id: util.hashString(),
        title: '',
        elements: [],
        swatches: [],
        styles: [],
        contexts: [],
        states: []
      }
    }
  }

  toggleLoad() {
    this.setState({showLoad: !this.state.showLoad})
  }

  toggleTutorial() {
    this.setState({showTutorials: !this.state.showTutorials})
  }

  updateTitle(title) {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)
      component.title = title

      return {
        component: component
      }
    })
  }

  loadComponent(component) {
    this.props.begin(component)
  }

  loadTutorial(tutorial) {
    let confirm = window.confirm('Warning! Tutorials do not follow your actions. When finished with an instruction, you must manually click the forward button to proceed.')

    if (confirm) {
      this.props.tutorial(tutorial)
      let component = this.state.component
      component.title = ''
      this.props.begin(this.state.component)
    }
  }

  create() {
    this.props.begin(this.state.component)
  }

  render() {
    return (
      <div className="Setup">
        <p className="intro">Welcome. To get started, choose one of the options below.</p>

        <div className="setup-body">
          <div className="setup-section">
            <h3 className="section-title">
              New
              <small>create a new component</small>
            </h3>

            <input placeholder="Enter a name" className="name-input" type="text" id="c_title" value={this.state.component.title} onChange={(e) => this.updateTitle(e.target.value)} />
            <button onClick={() => this.create()} className={`setup-button create ${this.state.component.title ? '' : 'disabled'}`}>Create</button>
          </div>

          <div className="setup-section">
            <h3 className="section-title">
              Load
              <small>open a saved component</small>
            </h3>

            <button className={`setup-button load
              ${this.state.showLoad ? 'open' : ''}
              ${this.state.components.length ? '' : 'disabled'}
              `} onClick={() => this.toggleLoad()}>
              Select a component
              <i className="fa fa-chevron-down"></i>

              <ul className="load-options">
                {this.state.components.map((component) => {
                  return (
                    <li onMouseDown={() => this.loadComponent(component)} className="load-option" key={component.id}>{component.title || 'Untitled Component'}</li>
                  )
                })}
              </ul>
            </button>
          </div>

          <div className="setup-section">
            <h3 className="section-title">
              Tutorial
              <small>take an interactive tour</small>
            </h3>

            <button className={`setup-button load
              ${this.state.showTutorials ? 'open' : ''}
              ${this.state.tutorials.length ? '' : 'disabled'}
              `} onClick={() => this.toggleTutorial()}>
              Select a tutorial
              <i className="fa fa-chevron-down"></i>

              <ul className="load-options">
                {this.state.tutorials.map((tutorial) => {
                  return (
                    <li onMouseDown={() => this.loadTutorial(tutorial)} className="load-option" key={tutorial.id}>{tutorial.title}</li>
                  )
                })}
              </ul>
            </button>
          </div>
        </div>
      </div>
    )
  }

}

export default Setup
